/* Section Styling */
.solution {
    /* padding: 4rem 0; */
    background-color: #131313;
    color: #fff;
    margin-bottom: 300px;
  }
  
  .section-header {
    text-align: left;
    margin-bottom: 16px;
    align-self: flex-start;
    font-size: 20px;
    font-weight: normal;
    color: #cacaca;
  }

  @media(min-width:801px){
    .section-header{
      max-width: 32vw;
      margin-left:10vw;
    }
  }

  @media(max-width:800px){
    .section-header{
      max-width: 100vw;
    }
  }
  
  .section-title {
    font-family: 'Outfit';
    font-size: 64px;
    font-weight: normal;
    line-height: 100%;
    margin-bottom: 14px;
    margin-top: 0;
  }
  
  .section-title span {
  }

  .section-subtitle{
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 4px;
  }
  
  .section-description {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.3px;
    font-weight: normal;
    /* margin-top: 1rem; */
    color: #cacaca;
    margin-bottom: 113px;
    margin-top: 0;

  }
  
  /* Cards Container */
  .cards-container {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    /* grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); */
    gap: 2rem;
    margin-top: 2rem;
  }
  
  /* Card Styling */
  .card {
    background: #151515;
    border-radius: 12px;
    padding: 2rem;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    position: relative;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  /* .card h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .card p {
    font-size: 1rem;
    color: #ccc;
    line-height: 1.5;
  } */
  
  /* Hover Effect */
  .card:hover {
    /* transform: translateY(-10px); */
    /* box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.7); */
  }
  
  /* Overlay for Card 1 */
  .card-overlay {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #fff;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    font-size: 0.9rem;
    box-shadow: 0px 0px 8px rgba(255, 255, 255, 0.3);
    display: none;
  }
  
  .card:nth-child(1):hover .card-overlay {
    display: block;
  }
  

  @media(max-width:800px){
    .cards-container{
      /* grid-template-columns: 1fr; */
      display: flex;
      flex-direction: column;
    }
  
    .solution{
      width: 100%;
      margin-bottom: 200px;
    }
  }


  @media(1200px <= width <= 1500px){

    .section-subtitle{
      font-size: 14px;
    }

    .section-title{
      font-size: 52px;
    }

    .section-description{
      font-size: 15px;
    }

  }