/* Swap Section Styling */
.fuse-section {
    padding-top: 80px;
    padding-right: 44px;
    padding-left: 44px;
    background: radial-gradient(circle at top left, rgba(23, 23, 23, 1) 0%, rgba(255, 255, 255, 0.05) 50%, rgba(23, 23, 23, 1) 100%);
    color: #fff;
    border-radius: 24px;
    gap: 2rem;
    min-height: 650px;
    position: relative;
    overflow: hidden;
  }

  .cardContent h3{
    font-family: 'Outfit', sans-serif;
    font-size: 44px;
    line-height: 100%;
    letter-spacing: 2%;
    font-weight: normal;
    text-align: left;
    margin-top: 0;
    margin-bottom: 24px;
  }

  .fuse-section .cardContent h3{
    max-width: 400px;
  }
    

  .cardContent p{
    font-family: 'Outfit', sans-serif;
    font-size:20px;
    font-weight: 300;
    line-height: 28px;
    text-align: left;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
  }

  .fuse-section .cardContent p{
    max-width: 80%;
  }

  .imageHolder{
    display: flex;
    margin-bottom: 60px;
  }

  .imageHolder img.main{
    max-height:30vw;
    max-width: 100%;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;    
  }

  .coinsCol{
    display: flex;
    flex-direction: column;
    gap: 55px;
    margin-right: 34px;
  }

  .usdcCol{
    display: flex;
    flex-direction: column;
    gap: 55px;
    position: relative;
  }

  .usdcTwo{
    margin-left: 125px;
    margin-top: 5px;
  }

  .usdcCol img{
    /* height: 41px;
    width: 41px; */
    z-index: 2;
  }

  .usdcOne img{
    height: 42px;
    width: 42px;
  }

  .usdcTwo img{
    height: 41px;
    width: 41px;
  }
  
  .usdcThree img{
    height: 42px;
    width: 42px;
    margin-top: 4px;
  }

  .dogHolder{
    display: flex;
    align-items: center;
    margin-left: 120px;
  }

  .dog .dogImg{
    height: 100%;
    max-height: 62px;
    /* max-height: 93px; */
    width: auto;
    /* margin-left: 120px; */
    /* z-index: 2; */
    transition: all 2s ease;

  }

  .usdcOne,
  .usdcTwo, 
  .usdcThree{
    position: relative;
    overflow: visible;
    display: flex;
    justify-content: start;
  }

  .usdcOne::before,
  .usdcThree::before,
  .usdcTwo::before{
    content: '';
    height: 1px;
    width: 125px;
    position: absolute;
    background-color: grey;
    z-index: 1;
    clip-path: inset( 0 100% 0 0);
    /* transition: clip-path 2s ease-out; */
    }

  .usdcThree::before{
    /* top: -70%;
    transform: rotate(-40deg);
    right: 19px; */
    top: -70%;
    transform: rotate(-40deg);
    left: 50%;

  }

  .usdcOne::before{
    /* top: 170%;
    transform: rotate(40deg);
    right: 19px; */
    top: 170%;
    transform: rotate(40deg);
    left: 50%;

  }

  .usdcTwo::before{
    top:50%;
    left: 100%;
  }

  .fuse-section:hover .usdcOne::before,
  .fuse-section:hover .usdcTwo::before,
  .fuse-section:hover .usdcThree::before{
    animation: leftToRight 2s ease 0s 1;
    clip-path: inset( 0 0 0 0);

  }

  .fuse-section:hover .dog .dogImg{
    max-height: 93px;
  }

  .fuse-section:hover .dog .dogBg{
    height: 200%;
    width: 200%;
    left: -50%;
    top: -50%;
  }

  .fuse-section:hover .dogText{
    font-size: 38px;
    margin-left: 25px;

  }

  .dogText{
    font-size: 25px;
    /* font-size: 38px; */
    line-height: 66px;
    font-weight: 500;
    margin-left: 16px;
    transition: all 2s ease;
    /* margin-left: 25px; */
  }

  .eth,
  .base,
  .solana{
    position: relative;
  }
  
  .eth::before{
    content: '';
    background: url("../assets/images/solutionDotted.png");
    top: 50%;
    left: 100%;
    position: absolute;
    height: 1px;
    width: 400px;
  }
  
  .base::before{
    content: '';
    background: url("../assets/images/solutionDotted.png");
    top: 50%;
    left: 100%;
    position: absolute;
    height: 1px;
    width: 400px;
  }
  
  .solana::before{
    content: '';
    background: url("../assets/images/solutionDotted.png");
    top: 50%;
    left: 100%;
    position: absolute;
    height: 1px;
    width: 400px;
  }


  .usdcOne {
    position: relative;
    width: fit-content;
  }
  .usdcOne::after {
    animation: growAndFade 3s infinite ease-out;
    background-color: dodgerblue;
    border-radius: 50%;
    height: 150%;
    opacity: 0;
    position: absolute;
    top: -25%;
    left: -25%;
    width: 150%;
    content:''
  }
  
  .usdcTwo {
    position: relative;
    width: fit-content;
  }
  .usdcTwo::after {
    animation: growAndFade 3s infinite ease-out;
    background-color: dodgerblue;
    border-radius: 50%;
    height: 150%;
    opacity: 0;
    position: absolute;
    top: -25%;
    left: -25%;
    width: 150%;
    content:''
  }
  
  .usdcThree {
    position: relative;
    width: fit-content;
  }
  .usdcThree::after {
    animation: growAndFade 3s infinite ease-out;
    background-color: dodgerblue;
    border-radius: 50%;
    height: 150%;
    opacity: 0;
    position: absolute;
    top: -25%;
    left: -25%;
    width: 150%;
    content:''
  }

  .fuse-section:hover .dog::before{
    /* content: '';
    height: 150%;
    width: 150%;
    border-radius: 50%;
    border: 2px solid #fff; */
  }
  
@keyframes growAndFade {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.dog{
  position: relative;
}

.dogBg{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  transition: all 3s ease;
}

.dogImg{
  position: relative;
  z-index: 3;
}


@media(max-width:1600px){
  .coinsCol{
    gap: 35px;
    margin-right: 34px;
  }

  .usdcCol{
    gap: 35px;
  }

  .usdcTwo{
    margin-left: 100px;
  }

}

  @media(max-width:800px){
    .fuse-section{
      padding: 20px 10px 0 10px;

    }
    .imageHolder img.main{
      max-height: -webkit-fill-available;
      margin: 10px;
    }
    .fuse-section .cardContent p{
      max-width: 100%;
    }

    .coinsCol{
      gap: 20px;
      margin-right: 12px;
    }
  
    .usdcCol{
      gap: 20px;
    }
  
    .usdcTwo{
      margin-left: 50px;
    }

    .eth img,
    .solana img,
    .base img{
      height: 30px;
      width: auto;
    }

    .usdcOne img,
    .usdcTwo img,
    .usdcThree img{
      height: 30px;
      width: auto;
    }

    .usdcOne::before{
      transform: rotate(45deg);
    }
    
    .usdcThree::before{
      transform: rotate(-45deg);
      top: -35%;
      left: 30%;
    }

    .usdcOne::before,
    .usdcTwo::before,
    .usdcThree::before{
      width: 70px;
    }

    .fuse-section .dogHolder{
      margin-left: 45px;
    }

    .fuse-section .dog .dogImg{
      max-height: 40px;
    }

    .fuse-section:hover .dog .dogImg{
      max-height: 50px;
    }

    .fuse-section:hover .dog .dogBg{

    }

    .fuse-section .dogText{
      font-size: 18px;
      margin-left: 10px;

    }

    .fuse-section:hover .dogText{
      margin-left: 12px;
      font-size: 24px;
    }

  }