@import url('https://fonts.googleapis.com/css2?family=Geist:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
  margin: 0;  
  font-family: 'Geist', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

*{
  transition: all 0.3s ease;
}


h1{
  font-family: 'Geist';
  font-size: 80px;
  line-height:100%;
  letter-spacing:0;
  font-weight: 300;
}

.textGradient{
  background: linear-gradient(to bottom, #fff,#c6c6c6, #7a7a7a, #333); /* Gradient colors */
  -webkit-background-clip: text; /* Clips the gradient to the text */
  -webkit-text-fill-color: transparent; /* Makes the rest of the text transparent */
}

.halfMoonLight, .halfMoonDark{
  height: 8px;
  width: 6px;
}

.halfMoonLight{
  filter: brightness(5);
}
