footer{
    background-color: #131313;
    padding:120px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.logoFooter{
    max-height: 175px;
}

.footerSocials{
    position: absolute;
    right: 5%;
    top:50%;
    transform: translateY(-50%);
}

.footerSocials .socialLogos img{
    height: 44px;
    width: 44px;
    cursor: pointer;
}

.footerSocials .socialLogos .discordFooter{
    margin-right: 24px;
}

.footerSocials p{
    font-family: 'Lato', sans-serif;
    font-size: 13px;
    font-weight: 900;
    text-transform: uppercase;
    text-align: left;
    color: #7d7d7d;
    line-height: 20px;
    letter-spacing: 1px; 
    margin-top: 0;
    margin-bottom: 23px;  
}

@media(max-width:1000px){
    footer{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
.logoFooter{
    max-height: 75px;
}
    .footerSocials{
        position: relative;
        transform: none;
        right: 0;
        top: 0;
        margin-top: 20px;
    }
    .footerSocials p{
        text-align: center;

    }
}