/* Swap Section Styling */
.swap-section {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 30px;
    padding-right: 31px;
    background: radial-gradient(circle at top left, rgba(23, 23, 23, 1) 0%, rgba(255, 255, 255, 0.05) 50%, rgba(23, 23, 23, 1) 100%);

    color: #fff;
    border-radius: 24px;
    gap: 2rem;
    min-height: 650px;
    position: relative;

  }

  .cardContent h3{
    font-family: 'Outfit', sans-serif;
    font-size: 44px;
    line-height: 100%;
    letter-spacing: 2%;
    font-weight: normal;
    text-align: left;
    margin-top: 0;
    margin-bottom: 24px;
  }

  .cardContent p{
    font-family: 'Outfit', sans-serif;
    font-size:20px;
    font-weight: 300;
    line-height: 28px;
    text-align: left;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
  }

  .swapImage{
    flex:1
  }

  .cardContent{
    flex:1

  }

  .swapImage img.main{
    max-height:30vw;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
    
  }

  .swapDiv{
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 38px;
    padding-top: 41px;
    position: absolute;
    bottom: 28px;
    left: 50%;
    transform: translateX(-50%);
    background: radial-gradient(circle at top left, rgba(23, 23, 23, 1) 0%, rgba(255, 255, 255, 0.05) 50%, rgba(23, 23, 23, 1) 100%);
    background-color: #131313;
  }


  .swapped{
    font-family: 'Outfit', sans-serif;
    font-size: 32px;
    line-height: 46px;
    letter-spacing: -1%;
    text-shadow: 0px 0px 10px #ffffff;
    margin: 0;
    padding: 0;
    margin-bottom: 19px;
  }

  .swapInnerDiv{
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 16px;
    margin-bottom: 43px;
    margin-left: 17px;
    margin-right: 17px;
    padding: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    position: relative;
    filter: blur(8px);
    transition: filter 0.5s ease-in-out;    
  }

  .swap-section:hover .swapInnerDiv{
    filter: none;
  }

  .swapInnerDiv span{
    font-family: 'Outfit',sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    z-index: 1;
  }

  .ponkeSwap{
    height: 32px;
    margin-right: 9px;
    z-index: 1;
  }

  .arrowRightGrey{
    height: 24px;
    margin: 0 32px;
    z-index: 1;
  }

  .skiIcon{
    height: 32px;
    margin-right: 14px;
    z-index: 1;
  }

  .tickIcon{
    height: 24px;
    margin-left: 32px;
    z-index: 1;
  }

  .swap-blur {
    /* background: radial-gradient(circle at top left, rgba(23, 23, 23, 1) 0%, rgba(255, 255, 255, 0.05) 50%, rgba(23, 23, 23, 1) 100%);
    background-color: #131313;
    filter: blur(20px);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom:0;
    right:0;
    inset: 0;
    z-index: 2;
    transition: transform 0.5s ease-in-out; */
  }
  
  
  .swap-info {
    max-width: 50%;
  }
  
  .swap-info h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }
  
  .swap-info p {
    font-size: 1rem;
    color: #aaa;
    line-height: 1.5;
  }
  
  .swap-card {
    background-color: #151515;
    border-radius: 12px;
    padding: 1.5rem;
    position: relative;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
    min-width: 300px;
    overflow: hidden;
  }
  
  .swap-status {
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 1rem;
    color: #fff;
    text-align: center;
  }
  
  .swap-blur-wrapper {
    position: relative;
    overflow: hidden;
  }
  
  /* .swap-blur {
    background-color: rgba(255, 255, 255, 0.1);
    filter: blur(8px);
    position: absolute;
    inset: 0;
    z-index: 1;
    transform: translateX(-100%);
    transition: transform 0.5s ease-in-out;
  } */
  
  /* .swap-card:hover .swap-blur {
    transform: translateX(100%);
  } */
  
  .swap-text {
    position: relative;
    z-index: 2;
    font-size: 1.2rem;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .swap-icon {
    width: 24px;
    height: 24px;
    margin: 0 0.5rem;
    vertical-align: middle;
  }
  

  @media(max-width:800px){
    .swap-section{
      flex-direction: column;
      max-width: 85vw;
      padding-right: 0px;
      padding-top: 0;
      gap: 0;
    }
    .swapImage{
      max-width: -webkit-fill-available;
    }
    .swapDiv{
      position: relative;
      /* transform: scale(0.7); */
      transform: none;
      bottom: 0;
      left: 0;
      margin-top: 8px;
      margin-left: 5px;
      margin-right: 5px;
    }

    .swapImage img.main{
      max-height: fit-content;
      width: 85vw;
      height: auto;
    }

    .arrowRightGrey{
      margin: 0 6px;
    }

    .tickIcon{
      margin: 0 6px;
    }

    .cardContent{
      padding: 20px;
    }
  }