.heroSection {
    text-align: center;
    margin-top: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
  }
  
  .heroSection h1 {
    /* font-size: 3rem; */
    margin-bottom: 0rem;
    margin-top: 0;
    z-index: 2;
  }
  
  h1 span {
    display: block;
    /* font-size: 3rem; */
    opacity: 0.8;
  }
  
  .heroSection p {
    font-size: 21px;
    margin: 32px 0;
    font-weight: 300;
    z-index: 2;
    letter-spacing: -0.75px;
    color: #EAEAEA;
  }
  
  .beta-button {
    background-color: #eaeaea;
    color: #000;
    border: none;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:4px;
    width: max-content;
    z-index: 2;
  }
  
  .icon-container {
    display: flex;
    justify-content: center;
    gap: 24px;
    margin-top: 30px;
    z-index: 2;
  }
  
  .icon {
    width: 44px;
    height: 44px;
    background-color: #333;
    border-radius: 50%;
  }
  
  .heroLight{
    width: 100vw;
    /* width: 0; */
    max-width: 100vw;
    margin-top:-45px;
    z-index: 1;
    /* animation: leftToRight 2s infinite 0 1;     */
    animation: leftToRight 3s ease-in 0s 1;
  } 

  /* img {
    object-fit: cover;
    animation: leftToRight 2s infinite;
  } */
  
  @keyframes leftToRight {
    0% {
      clip-path: inset(0 100% 0 0);
    }
    100% {
      clip-path: inset(0 0 0 0);
    }
  }

  @media(1200px <= width <= 1500px){
    .heroSection{
      margin-top: 185px;
    }

    .heroLight{
      margin-top: -85px;
    }

    h1{
      font-size: 78px;
    }
    .heroSection p {
      margin: 20px 0;
    }
  }

  @media(max-width:800px){
    .heroSection{
      margin-top: 150px;
    }
    .heroSection h1 {
      font-size: 3.5em;
    }

    .heroSection p{
      font-size: 16px;
    }

    .heroLight{
      margin-top: 0;
    }
  }