.traction{
    padding-top: 230px;
    background-color: #ececec;    
    padding-bottom: 300px;
}

@media(min-width: 1200px){
    .traction{
        padding-left: 15vw;
        padding-right: 15vw;
    }
}

@media(max-width: 1700px){
    .traction{
        padding-left: 10vw;
        padding-right: 10vw;
    }
}

.sectionHeader{
    background: linear-gradient(to top, #191919,#939392); /* Gradient colors */
    -webkit-background-clip: text; /* Clips the gradient to the text */
    -webkit-text-fill-color: transparent; /* Makes the rest of the text transparent */
}

 h4.sectionHeading{
    font-family: 'Outback', sans-serif;
    font-weight: normal;
    font-size: 52px;
    line-height: 100%;
    letter-spacing: 2%;
    margin-top: 16px;
    margin-bottom: 0;
}

.traction .sectionSubHeading{
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 4px;
}

p.sectionSubHeading{
    font-weight: normal;
    font-size: 14px;
    line-height: 130%;    
}

.statsParent{
  position: relative;
}

.statsHolder{
   /* From https://css.glass */
background: rgba(255, 255, 255, 0.31);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(14.1px);
-webkit-backdrop-filter: blur(14.1px);
/* border: 3px solid rgba(255, 255, 255, 1); */
    /* width: 100%; */
    margin-top: 50px;
    padding: 6%;
    display: flex;
    align-items: center;
    z-index: 4;
    position: relative;
    border: 3px solid transparent; /* Base border */
    border-radius: 22px;
    /* border-image: linear-gradient(to bottom right, rgba(255, 255, 255, 0) 0%, white 100%); */
    /* border-image-slice: 1; */

}

.statsHolder::before {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  border-radius: 22px; /* Matches the parent div's border-radius */
  padding: 3px; /* Thickness of the border */
  background: linear-gradient(to bottom right, rgba(255, 255, 255, 0) 0%, white 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0); /* Masks content area */
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  z-index: 2; /* Places it behind the content */
}

.statsHolder .innerDiv{
    width: 50%;
    color: #222222;
    padding-right: 34px;


}

.statsHolder .innerDiv p.statsDesc{
    font-size: 24px;
    font-weight: normal;
    line-height: 150%;
}

.stats{
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    justify-content: center;
    align-items: center;
}

.statNumber{
    font-weight: 600;
    font-size: 44px;
    line-height: 52px;
    letter-spacing: -0.25px;
    margin: 0;
    padding: 0;
    margin-bottom: 6px;
}

.statDetails{
    font-size: 20px;
    font-weight: normal;
    line-height: 150%;
    margin: 0;
    padding: 0;
    color: #535353;
    
}

.statOne, .statTwo{
    padding: 28px;
    border-top: 1px solid #cacaca;
    border-bottom: 1px solid #cacaca;
    height: -webkit-fill-available;
}

.statOne{
    border-right: 1px solid #cacaca;
}



.twitter-card {
    background-color: #eaeaea;
    border-radius: 16px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 400px;
    padding: 65px 32px 65px 40px;
    overflow: hidden;
    /* height: 600px; */
    position: absolute;
    left: 52%;
    z-index: 3;

  }
  
  .card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .user-avatar {
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
  
  .user-info {
    flex: 1;
    margin-left: 12px;
  }
  
  .user-name {
    font-weight: bold;
    font-size: 1rem;
    display: block;
    color: #131313;
  }
  
  .user-handle {
    font-size: 0.9rem;
    color: gray;
  }
  
  .close-button {
    font-size: 1.2rem;
    color: gray;
    cursor: pointer;
  }
  
  .card-body {
    margin: 12px 0;
    font-size: 20px;
    font-weight: normal;
    line-height: 130%;
    font-family: "Inter", sans-serif;
    color: #333;

  }
  
  .card-media {
    margin: 12px 0;
    position: relative;
  }
  
  .media-preview {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    width: 100%;
  }
  
  .media-image {
    display: block;
    width: 100%;
    border-radius: 12px;
  }
  
  .play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 255, 0.8);
    color: white;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    cursor: pointer;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  }
  
  .card-footer {
    display: flex;
    justify-content: space-between;
    font-size: 0.9rem;
    color: gray;
  }
  
  .card-footer span {
    cursor: pointer;
  }
  

  .spring{
    position: absolute;
    right: -9%;
    top: -45%;
    z-index: 1;

  }

  @keyframes rotate-orbit {
    0% {
      transform: rotate3d(1, 1, 0, -60deg);
    }
    50% {
      transform: rotate3d(1, 1, 0, 70deg) ;  
    }
    100% {
      transform: rotate3d(1, 1, 0, -60deg);
    }
  }

  .spring img{
    max-width: 400px;
    height: auto;
    z-index: 1;
    position: relative;
    /* animation: rotate-orbit 5s linear infinite; */

  }
  

  .metricsContainer{
    max-width: 100%;
  }

  @media(max-width: 800px){

    .traction{
      padding-top: 150px;
      padding-bottom: 350px;
    }
    .statsHolder{
      flex-direction: column;
    }
    .statsHolder .innerDiv{
      width: 100%;
    }

    .statsHolder .innerDiv{
      padding: 0;
    }

    .statsHolder .innerDiv p.statsDesc{
      margin-top: 0;
    }

    .twitter-card{
      position: relative;
      left: 0;
      top: 0;
      transform: none;
      width: 85vw;
      margin-top: 50px;
      margin-bottom: 35px;
      padding: 12px;
    }
    
     .spring{
      bottom: -14%;
      top: auto;
    }

    .spring img{
      max-width: 75vw;
    }

    .stats{
      display: flex;
      flex-direction: column;
    }

    .statTwo{
    border-left: 1px solid #cacaca;
    }
  }

  @media(1200px <= width <= 1500px){
    .sectionHeading{
      margin-top: 0 !important;
    }

    .statsHolder{
      margin-top: 30px;
      padding: 6% 3%;
    }

    .statsHolder .innerDiv p.statsDesc{
      margin-top: 0;
      margin-bottom: 22.5px;
      font-size: 20px;
    }

    .statNumber{
      font-size: 36px;
    }

    .statDetails{
      font-size: 15px;
    }

    .twitter-card{
      /* transform: scaleY(0.7) scaleX(0.8); */
      width: 320px;
      padding: 51px 26px 51px 32px;
    }

    .user-avatar{
      height: 40px;
      width: 40px;
    }

    .user-name{
      font-size: 12px;
    }

    .user-handle{
      font-size: 12px;
    }

    .post-text{
      font-size: 14px;
      line-height: 20px;
    }

    

    .traction{
      padding-left: 15vw;
      padding-right: 15vw;
    }

    .spring img{
      max-width: 300px;
    }
    
  }