.yield-section{
    background: url('../assets/images/SolutionCardThreeBg.png');
    /* height: 600px; */
    background-size: cover;
    background-position: top right;
    background-repeat: no-repeat;
    display: flex;
    align-items: flex-end;
    /* padding: 40px; */
    border-radius: 24px;
  overflow: hidden;  

}


.fourDiv{
  position: relative;
}

.divOne{
  z-index: 2;
  opacity: 0.5;
}

.rect{
  /* position: absolute;
  top: 40%;
  left: 60%;
  transform: translate(-50%,-40%);
  z-index: 3;
  opacity: 0.5; */
  /* max-width: 50%; */
}

.rectangle{
  position: absolute;
  top: 40%;
  left: 60%;
  transform: translate(-50%,-40%);
  z-index: 3;
  opacity: 0.5;
  max-width: 50%;

}

.rectLogo{
  position: absolute;
  top: 45%;
  left: 60%;
  transform: translate(-50%,-40%);
}

.yield-section:hover .divOne{
  transform: translate(15%,-20%);
  opacity: 1;
  transition: all 2s ease;  
  transition-delay: 1s;  
}


.yield-section:hover .rectangle{
  transform:  translate(-50%,-50%) ;
  opacity: 1;
  transition: all 1s ease;  
}

.yield-section:hover .rectLogo{
  transform:  translate(-50%,-70%) ;
  opacity: 1;
  transition: all 1s ease;  
}

.yield-section .cardText{
  padding: 40px;
  transform: translateY(-20%);

}

.yield-section .cardContent{
    height: -webkit-fill-available;
}

.yield-section .mainImage{
  max-width: 100%;
  object-fit: contain;
}

.yield-section .imageContainer{
  /* background: url('../assets/images/solutionThreeBg.png'); */
  width: 100%;
  object-fit: contain;

}

.divOne{
  max-width: 100%;
  transform: translate(10%,10%);
}

.yield-section .fourDiv img{
  /* transform: rotate(-25deg); */
}



@media(max-width:800px){
    .yield-section{
      padding: 0;
    }

    .yield-section .cardContent{
      padding: 0;
    }

.yield-section .rectLogo{
max-width: 70px;
}
  }