.navbar {
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 70%;
  max-width: 1200px;
  background-color: #151515;
  border-radius: 100px;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
  /* box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5); */
  transition: all 0.3s ease-in-out;
}

.navbar.open{
  
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: space-between;  
}

.logo {
  height: 32px;
}

.hamburger {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  display: none;
}

.menu-container {
  display: flex;
  align-items: center;
}

.menu {
  display: flex;
  list-style: none;
  gap: 32px;
  margin: 0;
  padding: 0;
}

.menu li {
  font-size: 1rem;
  cursor: pointer;
}

.nav-link {
  color: #7d7d7d;
  text-decoration: none;
  font-size: 1rem;
  padding: 0.5rem 0;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
  position: relative;
}

.nav-link.active {
  text-shadow: 0px 0px 18px #fff;
  color: #fff;
}

.nav-link.active::before {
  content: '';
  /* content: url('../assets/images/halfMoonLight.png'); */
  position: absolute;
  height: 8px;
  width: 6px;
  background: url('../assets/images/halfMoonDark.png');
  filter: brightness(5);
  background-size: contain;
  background-repeat: no-repeat;
  /* background: #fff; */
  border-radius: 50%;
  top: 50%;
  left: -10px;
  transform: translate(0, -50%);
  box-shadow: 0px 0px 10px 2px #fff;
}

.navbar-btn {
  background-color: #eaeaea;
  color: #000;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
}

.navbar-btn:hover {
  background-color: #e6e6e6;
}

.menu-container .navbar-btn{
display: none;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .navbar {
    width: 80%;
  }

  button.navbar-btn{
    display: none;
  }

  .menu-container {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    background-color: #151515;
    position: absolute;
    top: 100%;
    left: 0;
    width: 80vw;
    padding: 1rem 2rem;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    /* box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5); */
    transition: transform 0.3s ease-in-out;
  }

  .menu-container.open {
    display: flex;
  }

  .hamburger {
    display: block;
  }

  .menu {
    flex-direction: column;
    gap: 16px;
    width: 100%;
  }

  .navbar-btn {
    margin-top: 1rem;
    width: 100%;
  }

  .logo-container{
    width: 100%;
  }

  .menu-container.open .navbar-btn{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 80vw;

  }
}

