.backed-section{
    background: #ececec;
    width: 100%;
    gap: 32px;
    justify-content: flex-start;
    margin-bottom: 156px;
}

#backed{
    background: #ececec !important;
    max-width: 100%;
}

.backed-section .hoverSection{
    display: none;
}

.backed-section .leftDiv{
    display: grid;  
    grid-gap: 60px;  
    grid-template-columns: repeat(3, minmax(0, 1fr));
}

.backed-section .angelImage,
.backed-section .angelDiv {
    height: 182px;
    width: 182px;
}

.backed-section .angelImage{
    filter: grayscale();
}

.backed-section .leftDiv,
.backed-section .rightDiv{
    flex:1;
}

.backed-section .leftDiv{
    padding: 72px 65px;
    background: #ececec;
    border: 3px solid white;
    border-radius:24px;
    max-width: 615px;

}

.backed-section .rightDiv{
    max-width: 600px;
}
.backed-section .rightDiv .sectionHeader{
    text-align: left;
}

.descriptionFirst, .descriptionSecond{
    font-size: 24px;
    line-height: 150%;
    color: #222;
    text-align: left;
}

.backed-section .sectionSubHeading{
    margin: 14px 0 16px 0;
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 4px;
}

.backed-section .sectionHeading{
    margin: 0;
    line-height: 115%;
}


.hoverSection{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 0.5s ease;
}
.angelDiv{
    position: relative;
    overflow: hidden;
}

.angelDiv:hover .hoverSection{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    background: rgba(0, 0, 0, 0.31);
border-radius: 9px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(10px);
-webkit-backdrop-filter: blur(10px);
    /* padding: 20px; */
}

.infoHolder{
    margin: 10px;
}

.angelTwitterLogo{
    width: auto;
    height: 25px;
    margin: 10px;
}

.displayName{
    font-weight: 500;
    color: #ddd;
    font-size: 12px;
}

.username{
    color:#333;
    font-weight: 500;
    font-size: 12px;

}

.hoverSection img{
    
    /* transform: translate(70%,40%); */
}

@media(max-width:800px){
    .backed-section{
        flex-direction: column;
    }

    .backed-section .leftDiv{
        padding: 16px 14px;
        gap: 24px;
    }

    .backed-section .angelImage, 
    .backed-section .angelDiv{
        width: 90px;
        height: 90px;
    }
}

@media(1200px <= width <= 1500px){

    .descriptionFirst,
    .descriptionSecond{
        font-size: 18px;
    }
}